import { ItemType } from "generated/prisma";
import { CreateTicketItemDto, UpdateTicketItemDto, CreateBulkTicketItemsDto } from "../types/dtos/ticket-item.dto";

export type TicketItemForm = {
  ticketId: number;
  type: ItemType;
  productId: number;
  serviceId: number;
  quantity: number;
};

export type UpdateTicketItemForm = {
  quantity: number;
};

export type BulkTicketItemsForm = {
  ticketId: number;
  items: TicketItemForm[];
};

export const getDefaultTicketItemForm = (): TicketItemForm => ({
  ticketId: 0,
  type: ItemType.PRODUCT,
  productId: 0,
  serviceId: 0,
  quantity: 1
});

export const getDefaultUpdateTicketItemForm = (): UpdateTicketItemForm => ({
  quantity: 1
});

export const getDefaultBulkTicketItemsForm = (): BulkTicketItemsForm => ({
  ticketId: 0,
  items: []
});

export const convertTicketItemFormToDto = (form: TicketItemForm): CreateTicketItemDto => ({
  ticketId: form.ticketId,
  type: form.type,
  productId: form.productId || null,
  serviceId: form.serviceId || null,
  quantity: form.quantity 
});

export const convertUpdateTicketItemFormToDto = (form: UpdateTicketItemForm): UpdateTicketItemDto => ({
  quantity: form.quantity
});

export const convertBulkTicketItemsFormToDto = (form: BulkTicketItemsForm): CreateBulkTicketItemsDto => ({
  ticketId: form.ticketId,
  items: form.items.map(convertTicketItemFormToDto)
});